import React, { Component } from "react";
import styled from "styled-components";
import DetailTemp from "@demoComponents/DetailTemp";
import ApiTable from "@demoComponents/ApiTable";
import CodeDemo from "@demoComponents/CodeDemo";
import Demo1 from "./Demo1";
import Demo2 from "./Demo2";
import Demo3 from "./Demo3";
import Demo4 from "./Demo4";

const Description = styled.section``;
const HashLink = styled.a``;
const Column = styled.div``;

const Apis = styled.section`
  padding-top: 20px;
  width: 100%;
  clear: both;
  table {
    width: 100%;
  }
  td {
    padding: 4px 6px;
  }
`;

class Tooltip extends Component {
  render() {
    const demoList = [
      {
        display: <Demo1 />,
        description: "",
        code: `import React, { Component } from "react";
import { Tooltip } from "@es";

class Demo1 extends Component {
    render() {
    return (
        <Tooltip title="aosiaois">Tooltip will show on mouse enter.</Tooltip>
    );
    }
}
        `
      },
      {
        display: <Demo2 />,
        description: "12placement",
        code: `import React, { Component } from "react";
import { Tooltip } from "@es";

const grid = {
    TR: "topRight",
    Top: "top",
    TL: "topLeft",
    BR: "bottomRight",
    Bottom: "bottom",
    BL: "bottomLeft",
    LT: "leftTop",
    Left: "left",
    LB: "leftBottom",
    RT: "rightTop",
    Right: "right",
    RB: "rightBottom"
};

class Demo2 extends Component {
    render() {
    return (
        <Grid>
            {Object.keys(grid).map((s, i) => {
                return (
                    <Tooltip key={i} title={"hello world"} placement={grid[s]}>
                        <Cell area={s}>{s}</Cell>
                    </Tooltip>
                );
            })}
        </Grid>
    );
    }
}
        `
      },
      {
        display: <Demo4 />,
        description:
          "SimpleTooltip, cssspan",
        code: `import React, { Component } from "react";
import { SimpleTooltip } from "@es";
import styled from "styled-components";

const Cell = styled.div\`\`;
const Grid = styled.div\`
  display: grid;
  grid-gap: 5px;
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: repeat(5, 80px);
  grid-template-areas:
    ". TL Top TR ."
    "LT . . . RT"
    "Left . . . Right"
    "LB . . . RB"
    ". BL Bottom BR .";
  $\{Cell\} {
    width: 100%;
    text-align: center;
    padding: 0;
    align-self: center;
    justify-self: center;
    background-color: #ddd;
    padding: 5px;
    border-radius: 5px;
    box-sizing: border-box;
    font-family: sans-serif;
    cursor: pointer;
  }
\`;

const grid = {
  TR: "topRight",
  Top: "top",
  TL: "topLeft",
  BR: "bottomRight",
  Bottom: "bottom",
  BL: "bottomLeft",
  LT: "leftTop",
  Left: "left",
  LB: "leftBottom",
  RT: "rightTop",
  Right: "right",
  RB: "rightBottom"
};

class Demo4 extends Component {
  render() {
    return (
      <Grid>
        {Object.keys(grid).map((s, i) => {
          return (
            <SimpleTooltip
              key={i}
              title={"hello world"}
              placement={grid[s]}
              defaultStyles={\`grid-area: $\{s\};\`}
            >
              <Cell>{s}</Cell>
            </SimpleTooltip>
          );
        })}
      </Grid>
    );
  }
}
`
      },
      {
        display: <Demo3 />,
        description: " arrowPointAtCenter ",
        code: `import React, { Component } from "react";
import { Tooltip } from "@es";

class Demo1 extends Component {
  render() {
    return (
      <div>
        <Tooltip placement="topLeft" title="Prompt Text">
          <span>Align edge / </span>
        </Tooltip>
        <br />
        <Tooltip placement="bottomLeft" title="Prompt Text" arrowPointAtCenter>
          <span>Arrow points to center / </span>
        </Tooltip>
      </div>
    );
  }
}`
      }
    ];

    return (
      <DetailTemp title={"Tooltip"}>
        <Description>
          <h2></h2>
          <p>
            1. 
            <br />
            2. 
            title //
          </p>
        </Description>
        <Column className={"demo-column"}>
          {demoList
            //   .filter((ele, index) => index % 2 === 0)
            .map((ele, index) => (
              <CodeDemo display={ele.display} description={ele.description} key={index}>
                {ele.code}
              </CodeDemo>
            ))}
        </Column>
        <Apis>
          <h2>Tooltip</h2>
          <ApiTable
            data={[
              {
                name: "className",
                description: " class",
                type: "string",
                default: ""
              },
              {
                name: "defaultStyles",
                description: "",
                type: "string",
                default: ""
              },
              {
                name: "visible",
                description: "",
                type: "boolean",
                default: ""
              },
              {
                name: "defaultVisible",
                description: "",
                type: "boolean",
                default: ""
              },
              {
                name: "theme",
                description: "",
                type: " dark light",
                default: "dark"
              },
              {
                name: "children",
                description: "",
                type: "node",
                default: ""
              },
              {
                name: "title",
                description: "",
                type: "node",
                default: ""
              },
              {
                name: "mouseEnterDelay",
                description: " Tooltip",
                type: "number",
                default: "0.1"
              },
              {
                name: "mouseLeaveDelay",
                description: " Tooltip ",
                type: "number",
                default: "0.1"
              },
              {
                name: "placement",
                description:
                  " top left right bottom topLeft topRight bottomLeft bottomRight leftTop leftBottom rightTop rightBottom",
                type: "string",
                default: "top"
              },
              {
                name: "enterAnimation",
                description: ",objectstyled-components  keyframes ",
                type: "node",
                default: ""
              },
              {
                name: "leaveAnimation",
                description: ",objectstyled-components  keyframes ",
                type: "node",
                default: ""
              },
              {
                name: "onVisibleChange",
                description: "",
                type: "(visible) => void",
                default: ""
              },
              {
                name: "autoAdjustOverflow",
                description: "",
                type: "boolean",
                default: "true"
              },
              {
                name: "arrowMargin",
                description: "",
                type: "number",
                default: "4"
              }
            ]}
          />
        </Apis>
        <Apis>
          <h2>Tooltip</h2>
          <ApiTable
            data={[
              {
                name: "arrowPointAtCenter",
                description: "",
                type: "boolean",
                default: "false"
              },
              {
                name: "getContainer",
                description: " body ",
                type: "function",
                default: "()=>document.body"
              }
            ]}
          />
        </Apis>
        <Apis>
          <h2>SimpleTooltip</h2>
          <ApiTable
            data={[
              {
                name: "arrowPointAtCenter",
                description: "",
                type: "boolean",
                default: "false"
              }
            ]}
          />
        </Apis>
      </DetailTemp>
    );
  }
}

export default Tooltip;
