import React, { Component } from "react";
import styled from "styled-components";
import DetailTemp from "@demoComponents/DetailTemp";
import ApiTable from "@demoComponents/ApiTable";
import CodeDemo from "@demoComponents/CodeDemo";
import Demo1 from "./Demo1";
import Demo2 from "./Demo2";
import Demo3 from "./Demo3";
import Demo4 from "./Demo4";
import Demo5 from "./Demo5";
import Demo6 from "./Demo6";
import Demo7 from "./Demo7";
import Demo8 from "./Demo8";

const Description = styled.section``;
const HashLink = styled.a``;
const Column = styled.div``;

const Apis = styled.section`
  padding-top: 20px;
  width: 100%;
  clear: both;
  table {
    width: 100%;
  }
  td {
    padding: 4px 6px;
  }
`;

class Steps extends Component {
  render() {
    const demoList = [
      {
        display: <Demo1 />,
        description: "",
        code: `import React, { Component } from "react";
import { Steps } from "@es";

class Demo1 extends Component {
    render() {
        return (
            <Steps current={1}>
                <Steps.Step title="Finished" description="This is a description." />
                <Steps.Step title="Progress" description="This is a description." />
                <Steps.Step title="Waiting" description="This is a description." />
            </Steps>
        );
    }
}`,
      },
      {
        display: <Demo3 />,
        description: "",
        code: `import React, { Component } from "react";
import { Steps } from "@es";

class Demo3 extends Component {
    render() {
        return (
            <Steps direction="vertical" current={1}>
                <Steps.Step title="Finished" description="This is a description." />
                <Steps.Step title="Progress" description="This is a description." />
                <Steps.Step title="Waiting" description="This is a description." />
            </Steps>
        );
    }
}
        `,
      },
      {
        display: <Demo2 />,
        description:
          ",",
        code: `import React, { Component } from "react";
import { Steps, Btn1, Btn2 } from "@es";

const Step = Steps.Step;

const steps = [
    {
        title: "First",
        content: "First-content"
    },
    {
        title: "Second",
        content: "Second-content"
    },
    {
        title: "Last",
        content: "Last-content"
    }
];

class Demo2 extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            current: 0
        };
    }

    next() {
        const current = this.state.current + 1;
        this.setState({ current });
    }

    prev() {
        const current = this.state.current - 1;
        this.setState({ current });
    }

    render() {
        const { current } = this.state;
        return (
            <div>
            <Steps current={current}>
                {steps.map(item => (
                    <Step key={item.title} title={item.title} />
                ))}
            </Steps>
            <div>{steps[current].content}</div>
            <div>
                {current < steps.length - 1 && (
                    <Btn1  onClick={() => this.next()}>
                        Next
                </Btn1>
                )}
                {current === steps.length - 1 && (
                    <Btn1 onClick={() => message.success("Processing complete!")} >
                        Done
                    </Btn1>
                )}
                {current > 0 && (
                    <Btn2 style={{ marginLeft: 8 }} onClick={() => this.prev()}>
                        Previous
                    </Btn2>
                )}
            </div>
            </div>
        );
    }
}
`,
      },
      {
        display: <Demo4 />,
        description: "",
        code: `import React, { Component } from "react";
import { Steps } from "@es";

class Demo3 extends Component {
    render() {
        return (
            <Steps direction="vertical" current={1}>
                <Steps.Step title="Finished" description="This is a description." />
                <Steps.Step title="Progress" description="This is a description." />
                <Steps.Step title="Waiting" description="This is a description." />
            </Steps>
        );
    }
}
        `,
      },
      {
        display: <Demo5 />,
        description: "",
        code: `import React, { Component } from "react";
import { Steps } from "@es";

class Demo4 extends Component {
    render() {
        return (
            <Steps type="dot" current={1}>
                <Steps.Step title="Finished" description="This is a description." />
                <Steps.Step title="Progress" description="This is a description." />
                <Steps.Step title="Waiting" description="This is a description." />
            </Steps>
        );
    }
}
`,
      },
      {
        display: <Demo6 />,
        description: "",
        code: `import React, { Component } from "react";
import { Steps } from "@es";

class Demo4 extends Component {
    render() {
        return (
            <Steps type="dot" direction="vertical" current={1}>
                <Steps.Step title="Finished" description="This is a description." />
                <Steps.Step title="Progress" description="This is a description." />
                <Steps.Step title="Waiting" description="This is a description." />
            </Steps>
        );
    }
}
`,
      },
      {
        display: <Demo7 />,
        description: "Icon",
        code: `import React, { Component } from "react";
import { Steps } from "@es";
import Male  from "@es/components/icons/male";
import Collection  from "@es/components/icons/Collection";

class Demo7 extends Component {
  render() {
    return (
      <Steps type="dot" icon={<Collection />} current={1}>
        <Steps.Step
          title="Finished"
          description="This is a description."
          icon={<Male />}
        />
        <Steps.Step title="Progress" description="This is a description." />
        <Steps.Step title="Waiting" description="This is a description." />
      </Steps>
    );
  }
}
`,
      },
      {
        display: <Demo8 />,
        description: "descriptiontitle",
        code: `import React, { Component } from "react";
import { Steps } from "@es";

class Demo8 extends Component {
  render() {
    return (
      <Steps type="arrow" current={1}>
        <Steps.Step description="This is a description." />
        <Steps.Step description="This is a description." />
        <Steps.Step description="This is a description." />
      </Steps>
    );
  }
}
`,
      },
    ];

    return (
      <DetailTemp title={"Steps"}>
        <Description>
          <h2></h2>
          <p>
            1. 
            <br />
            2. <HashLink href="#api">api</HashLink>
          </p>
        </Description>
        <Column className={"demo-column"}>
          {demoList
            //   .filter((ele, index) => index % 2 === 0)
            .map((ele, index) => (
              <CodeDemo
                display={ele.display}
                description={ele.description}
                key={index}
              >
                {ele.code}
              </CodeDemo>
            ))}
        </Column>
        <Apis>
          <h2>Steps</h2>
          <ApiTable
            data={[
              {
                name: "className",
                description: " class",
                type: "string",
                default: "",
              },
              {
                name: "defaultStyles",
                description: "",
                type: "string",
                default: "",
              },
              {
                name: "children",
                description: "",
                type: "node",
                default: "",
              },
              {
                name: "size",
                description: ", px",
                type: "number",
                default: "24",
              },
              {
                name: "type",
                description: ' "basic""dot"  "arrow"',
                type: "string",
                default: "basic",
              },
              {
                name: "current",
                description:
                  " 0  Step  status ",
                type: "number",
                default: "0",
              },
              {
                name: "direction",
                description:
                  "horizontalvertical",
                type: "string",
                default: "horizontal",
              },
              {
                name: "labelPlacement",
                description:
                  " vertical upAndDown ",
                type: "string",
                default: "horizontal",
              },
              {
                name: "status",
                description:
                  " wait|process|finish|error",
                type: "string",
                default: "",
              },
              {
                name: "initial",
                description: " 0  ",
                type: "number",
                default: "0",
              },
            ]}
          />
        </Apis>
        <Apis>
          <h2>Steps.Step</h2>
          <ApiTable
            data={[
              {
                name: "title",
                description: " ",
                type: "string|ReactNode",
                default: "",
              },
              {
                name: "description",
                description: " ",
                type: "string|ReactNode",
                default: "",
              },
              {
                name: "icon",
                description: (
                  <div>
                    (currentIcon, (index,
                    status, title, description) )=&gt;ReactNode
                    <br />
                    currentIcon 
                    Steps.type 'dot'currentIcon  Dot 
                  </div>
                ),
                type: "string | ReactNode | function ",
                default: "",
              },
              {
                name: "status",
                description:
                  " Steps  current wait|process|finish|error",
                type: "string",
                default: "wait",
              },
            ]}
          />
        </Apis>
      </DetailTemp>
    );
  }
}

export default Steps;
